import { useState } from 'react';
import './App.scss';
import { MoviesList } from './components/MoviesList';
import moviesFromServer from './api/movies.json';

function getMovieList(movies, { query }) {
  let movieList = [...movies];

  if (query) {
    movieList = movieList.filter((movie) => {
      const { title, description } = movie;
      const trimmedQuery = query.trim().toLowerCase();

      const titleMatch = title.toLowerCase().includes(trimmedQuery);
      const descriptionMatch = description.toLowerCase().includes(trimmedQuery);

      return titleMatch || descriptionMatch;
    });
  }

  return movieList;
}

export const App = () => {
  const [query, setQuery] = useState('');
  const visibleMovies = getMovieList(moviesFromServer, { query });

  return (
    <div className="page">
      <div className="page-content">
        <div className="box">
          <div className="field">
            {/* eslint-disable-next-line jsx-a11y/label-has-associated-control */}
            <label htmlFor="search-query" className="label">
              Search movie
            </label>

            <div className="control">
              <input
                type="text"
                id="search-query"
                className="input"
                placeholder="Type search word"
                onChange={(event) => {
                  setQuery(event.target.value);
                }}
              />
            </div>
          </div>
        </div>

        <MoviesList movies={visibleMovies} />
      </div>

      <div className="sidebar">
        Sidebar goes here
      </div>
    </div>
  );
};
